import {
		mapState,
		mapMutations
    } from 'vuex';
import md5 from '../../kayida/common/md5.js'
import uniPopup from "@/components/uni-popup/uni-popup.vue"

export default {
	components:{uniPopup},
	computed: {
	            ...mapState(['userInfo',])  
	        },
	data() {
		return {
			count_list:'',
			upuser:'',
			hasUpuser:0,
			m:true,
			weixin_pic:'',
			popup:0
		}
	},
	methods: {
		callPhone(phone) {
			uni.makePhoneCall({
			    phoneNumber: phone, 
			});
		},
		toStatis() {
			uni.navigateTo({
				url:"../statistics/statistics"
			})
		},
		toTeam() {
			uni.navigateTo({
				url:"../team/team"
			})
		},
		toOrder() {
			uni.navigateTo({
				url:"../teamorder/teamorder"
			})
		},
		open(){
			this.$refs.popup.open()
			uni.setClipboardData({
			    data: this.upuser.mobile,
			    success: function () {
					uni.showToast({
						title: '复制成功',
						duration:2000,
						icon:"none"
					})
			    }
			});
		},
		close() {
			this.$refs.popup.close()
		},
		downPic() {
			uni.downloadFile({
				url: this.weixin_pic,
				success: (res) =>{
					if (res.statusCode === 200){
						uni.saveImageToPhotosAlbum({
							filePath: res.tempFilePath,
							success: function() {
								uni.showToast({
									title: "保存成功",
									icon: "none"
								});
							},
							fail: function() {
								uni.showToast({
									title: "保存失败，请稍后重试",
									icon: "none"
								});
							}
						});
					}
				}
			})
		}
	},
	onLoad() {
		let data = {
			user_id: this.userInfo.user_id,
			token: this.userInfo.token,
			sign: md5(this.userInfo.user_id+this.userInfo.token)
		}
		this.request.httpRequest('api/team/index',data,'GET').then(res =>{
				if(res.data.code === 1000){
					this.upuser = res.data.retval.upuser
					this.count_list = res.data.retval.count_list
					this.hasUpuser = res.data.retval.hasUpuser
					this.weixin_pic = res.data.retval.upuser.wxqr
				}else{
					uni.showToast({
						title:res.data.msg,
						icon: "none"
					})
				}
			}
		)
	}
}
